.footer__caption {
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
  max-width: 318px;
  margin-top: 0px;
  margin-bottom: 0px;
}

@media screen and (max-width:660px) {
  .footer__caption {
    font-size: 14px;
    line-height: 17px;
    max-width: 282px;
  }
}